import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import { chatApi } from '../../utils/apiClient';

// Thunk  to phng chat mi
export const createChatRoom = createAsyncThunk(
    'chats/createChatRoom',
    async (chatData, { rejectWithValue }) => {
        try {
            const response = await chatApi.createChatRoom(chatData); // Gi API  to phng chat
            return response.data; // Tr v d liu chat room t server
        } catch (error) {
            return rejectWithValue(error.response?.data || error.message); // Tr v li nu c
        }
    }
);

// Thunk  ly danh sch phng chat ca ngi dng
export const getUserChats = createAsyncThunk(
    'chats/getUserChats',
    async (_, { rejectWithValue }) => {
        try {
            const response = await chatApi.getUserChats(); // Gi API  ly danh sch phng chat
            return {
                chatRooms: response.data.chatRooms, // Danh sch phng chat
                userId: response.data.userId,       // userId ca ngi dng
            };
        } catch (error) {
            return rejectWithValue(error.response?.data || error.message); // Tr v li nu c
        }
    }
);

// Slice cho chc nng chat
const chatSlice = createSlice({
    name: 'chats',
    initialState: {
        chatRooms: [],           // Danh sch phng chat
        currentChatRoom: null,   // Phng chat hin ti
        userId: null,            // ID ca ngi dng
        loading: false,          // Trng thi ti
        error: null,             // Thng bo li
    },
    reducers: {
        setCurrentChatRoom: (state, action) => {
            state.currentChatRoom = action.payload; // Cp nht phng chat hin ti
        },
    },
    extraReducers: (builder) => {
        builder
            // X l action 'createChatRoom'
            .addCase(createChatRoom.pending, (state) => {
                state.loading = true; // ang to phng chat
                state.error = null;
            })
            .addCase(createChatRoom.fulfilled, (state, action) => {
                state.loading = false; // To phng chat thnh cng
                state.chatRooms.push(action.payload.chatRoom); // Thm phng chat mi vo danh sch
            })
            .addCase(createChatRoom.rejected, (state, action) => {
                state.loading = false; // Li khi to phng chat
                state.error = action.payload || 'Failed to create chat room'; // Hin th li
            })

            // X l action 'getUserChats'
            .addCase(getUserChats.pending, (state) => {
                state.loading = true; // ang ti danh sch phng chat
                state.error = null;
            })
            .addCase(getUserChats.fulfilled, (state, action) => {
                state.loading = false; // Ly danh sch phng chat thnh cng
                state.chatRooms = action.payload.chatRooms; // Cp nht danh sch phng chat
                state.userId = action.payload.userId; // Cp nht userId
            })
            .addCase(getUserChats.rejected, (state, action) => {
                state.loading = false; // Li khi ly danh sch phng chat
                state.error = action.payload || 'Failed to fetch chat rooms'; // Hin th li
            });
    },
});

// Export actions  s dng trong cc component React
export const { setCurrentChatRoom } = chatSlice.actions;

// Export reducer  tch hp vo store
export default chatSlice.reducer;
